div[data-wpview-type="gallery"][data-wpview-text*="slider%3D%22true"] {
  padding: calc(56.25% - 2px) 0 0;
  background-color: rgba(0, 0, 0, 0.1);
  overflow: hidden; }
  div[data-wpview-type="gallery"][data-wpview-text*="slider%3D%22true"] .gallery {
